.background{
	font-family: 'Monofett', cursive;
	
}

body{
	background: -webkit-gradient(linear, left top, left bottom, from(#81addb),color-stop(30%, #8599be),color-stop(70%, #eb9291),to(#eb7));
	background: -webkit-linear-gradient(top, #81addb 0%,#8599be 30%,#eb9291 70%,#eb7 100%);
	background: -o-linear-gradient(top, #81addb 0%,#8599be 30%,#eb9291 70%,#eb7 100%);
	background: linear-gradient(to bottom, #81addb 0%,#8599be 30%,#eb9291 70%,#eb7 100%);
	 background-repeat: no-repeat;
    background-attachment: fixed;
}


.siz{
		height:15vh;
		}

.pos{		
			position:fixed;
			width:100vw;
			background: #81addb; 
			-webkit-box-shadow: 0 8px 6px -6px black; 
			        box-shadow: 0 8px 6px -6px black;
		}

.flexRowContainer{
	display: -ms-flexbox;
	display: flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-wrap:wrap;
      flex-wrap:wrap;
  -ms-flex-pack:justify;
      justify-content:space-between;

}

@media only screen and (max-width: 700px){
	.siz{
			height:20vh
		}

		.pos{
			margin-top:-2vh;
			-webkit-transform: scale(0.7, 0.7);
			    -ms-transform: scale(0.7, 0.7);
			        transform: scale(0.7, 0.7);
		}
}


